ale; \n  text-rendering: optimizeLegibility;\n}\n\nimg {\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n}\n\nbutton {\n  -webkit-tap-highlight-color: transparent;\n}\n\nbody {\n  font-family: \"Crimson Text\", sans-serif;\n  min-height: 100vh;\n  overflow-x: hidden;\n  background-color: #fefefe;\n}\n\n.heading {\n  font-family: \"Carnevalee Freakshow\", serif;\n  font-size: 64px;\n  line-height: 74px;\n  font-style: normal;\n  font-weight: 400;\n  font-weight: normal;\n  color: #542851;\n\n  flex: none;\n}\n\n.social-links {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  gap: 8px;\n\n  flex: none;\n\n  &__icon {\n    @include button(40px, 40px, 70%, rgba(black, 0.1));\n\n    flex: none;\n  }\n}\n\n.about {\n  @include flex-container(column, start, center);\n  padding: 32px 24px;\n  gap: 12px;\n\n  .about__bg {\n    position: absolute;\n    z-index: -1;\n    top: 0;\n    left: 0;\n    right: 0;\n\n    height: 588.5px;\n    filter: drop-shadow(0 4px 4px rgba(black, 0.5));\n\n    &__overlay,\n    &__img {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      clip-path: polygon(0% 0%, 0% 100%, 100% 69%, 100% 0%);\n    }\n\n    &__overlay {\n      background-color: #5A064B;\n      opacity: 0.6;\n    }\n\n    &__img {\n      opacity: 0.3;\n      object-fit: cover;\n      object-position: top left;\n    }\n  }\n\n  .about__box {\n    @include flex-container(column, start, center);\n    gap: 32px;\n\n    .about__avatar {\n      position: relative;\n      width: 354px;\n      height: 499px;\n\n      background: radial-gradient(50% 50% at 50% 50%, #3D558A 0%, #1C2C50 100%);\n      box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.5);\n      border-radius: 16px;\n\n\n      img {\n        width: 284.62px;\n        height: 437px;\n\n        position: absolute;\n        bottom: 0;\n        right: 0;\n        \n        filter: drop-shadow(0px 0px 45.6px rgba(245, 239, 168, 0.3));\n\n      }\n\n      h1 {\n        position: absolute;\n        width: 300px;\n        height: 168px;\n        top: 326px;\n        left: 18px;\n\n        font-family: 'Dancing Script';\n        font-style: normal;\n        font-weight: 500;\n        font-size: 72px;\n        line-height: 117%;\n\n        color: #FFFFFF;\n\n        filter: drop-shadow(0 0 2px rgba(black, 1)) ;\n        text-shadow: 0px 0px 12px rgba(0, 0, 0, 1);\n\n      }\n    }\n\n    .about__text-box {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      padding: 0px;\n      gap: 16px;\n\n      flex: none;\n      align-self: stretch;\n\n      .about__text {\n        font-family: 'Crimson Text';\n        font-style: normal;\n        font-weight: 600;\n        font-size: 28px;\n        line-height: 129%;\n        color: #777777;\n\n        flex: none;\n        align-self: stretch;\n      }\n    }\n  }\n}\n\n"],"sourceRoot":""}]);
